  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "CabinetGrotesk-Regular", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #343633;
  font-size: 1.5rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "ClashDisplay-Regular", sans-serif;
  margin: 2.5rem 0;
  font-weight: 700;
  text-align: center;
  color: #343633;
}

h1 {
  font-size: 16rem;
}

h2 {
  font-size: 10rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2rem;
  font-weight: 700;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.btn {
  width: 209px;
  height: 40px;
  border-radius: 280px;
  border: none;
  color: #ffffff;
  font-family: "ClashDisplay-Regular", sans-serif;
  font-weight: bold;
  background: linear-gradient(90deg, rgb(255, 105, 95) 0%, rgb(227, 23, 10) 100%);
  transition: background 1s ease;
}
.btn:hover {
  cursor: pointer;
  background: linear-gradient(90deg, rgb(248, 71, 59) 0%, rgb(193, 31, 20) 100%);
}

.proyecto__btn {
  margin-bottom: 20px;
}

.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 20px;
}
.navbar a,
.navbar input {
  font-family: "ClashDisplay-Regular", sans-serif;
  font-weight: bold;
  color: #343633;
  text-decoration: none;
}
.navbar input {
  background: none;
  border: none;
}
.navbar input:hover {
  cursor: pointer;
}

.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.overlay-content {
  position: absolute;
  background-color: white;
  height: 100%;
  display: flex;
  flex-direction: column;
  font-size: 100px;
}
.overlay-content a {
  font-family: "ClashDisplay-Regular", sans-serif;
  font-weight: bold;
  color: #343633;
  transition: background-color 0.5s;
  padding: 0 10px;
}
.overlay-content a:hover {
  color: #ffffff;
  background: linear-gradient(90deg, rgb(248, 71, 59) 0%, rgb(193, 31, 20) 100%);
}
.overlay-content input {
  text-align: end;
  background: none;
  border: none;
  color: #ffffff;
  font-size: 30px;
  font-weight: bold;
  padding: 10px;
}
.overlay-content input:hover {
  cursor: pointer;
}

.presentacion {
  background-image: url(/build/img/presentacion__fondo.svg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 80vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.presentacion h2 {
  margin: 0;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.presentacion p {
  width: 50%;
  text-align: center;
}
.presentacion .presentacion__enlaces a {
  font-family: "ClashDisplay-Regular", sans-serif;
  font-weight: bold;
  color: #343633;
}

.proyectos {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #343633;
}
.proyectos a:visited {
  color: #5B5B5B;
}
.proyectos .enlace__pequenio,
.proyectos .enlace__grande {
  text-decoration: none;
  font-family: "ClashDisplay-Regular", sans-serif;
  font-weight: bold;
  color: #5B5B5B;
}
.proyectos .enlace__pequenio {
  font-size: 40px;
}
.proyectos .enlace__pequenio--arriba,
.proyectos .enlace__grande--arriba,
.proyectos .enlace__grande--abajo,
.proyectos .enlace__pequenio--abajo {
  position: relative;
}
.proyectos .enlace__grande {
  font-size: 68px;
}
.proyectos .enlace__grande--abajo {
  right: 5%;
  margin: 0;
}
.proyectos .enlace__pequenio--abajo {
  top: -35px;
  left: 15%;
  margin: 0;
}
.proyectos h2 {
  color: #ffffff;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  margin: 0;
}
.proyectos .mockups {
  display: flex;
  flex-direction: row;
}
.proyectos .proyecto__mockup {
  transition: width 0.5s;
}
.proyectos .proyecto__mockup:hover {
  width: 105%;
  height: auto;
}

.habildades h2 {
  color: #343633;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  margin: 0;
}
.habildades .habilidades__presentacion,
.habildades .habilidades__herramientas {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.habildades img {
  width: 427px;
  height: 424px;
  object-fit: cover;
  border-radius: 8px;
  margin: 5px;
}
.habildades .contenedor__imagen {
  position: relative;
  display: inline-block;
}
.habildades .texto {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 48px;
  text-align: center;
  z-index: 1;
  /* Asegura que el texto est√© sobre la imagen */
}
.habildades .habilidades__herramientas {
  margin: 30px 0;
}
.habildades .habilidades__herramientas img {
  width: 101px;
  height: auto;
  object-fit: cover;
}